	module.exports = {"data":{"markdownRemark":{"html":"<p>The journalism department wants to write my students to come to me and ask some questions about myself. Because it is the order of the Municipal Higher Education Bureau, it cannot be accepted. She asked me what I meant about \"Fudan Complex\". I said that I don't have such poetic feelings. I just think that Fudan has a circle of life that I can't leave, maybe in the red tone of Fudan, maybe in these lives. In the friendship of sports, perhaps in this blue sky. People's lives must always be pinned. After losing their parents, you will suddenly feel that such sustenance is important. There are no children, no family warmth, and the only sustenance is where life is consumed. There may be many people in the world like this. This is the structure of life.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0065.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0065.md","frontmatter":{"title":"April 2nd Saturday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0065.md absPath of file >>> MarkdownRemark"}}

